.App {
    /* background-color: #282c34; */
    background: url(../public/bg.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: white;
}

.Terminal {
    display: flex;
    background-color: #2e2f30;
    width: 650px;
    height: 450px;
    border-radius: 10px;

    color: green;
}

.Top {
    background-color: #302e2d;
    width: 100%;
    height: 25px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom: solid 1px #090909;
}
.BtnSection {
    /* background-color: blueviolet; */
    width: 70px;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.ControlBtn {
    border: 0;
    width: 12px;
    height: 12px;
    border-radius: 20px;
}
.red {
    background-color: #ed6a5e;
}

.yellow {
    background-color: #f5be4f;
}

.green {
    background-color: #61c454;
}
